@-webkit-keyframes aboutFadeIn {
    from {-webkit-transform: scale3d(1, 1, 1);transform: scale3d(1, 1, 1); opacity: 0;}
    30% {-webkit-transform: scale3d(1.25, 0.75, 1);transform: scale3d(1.25, 0.75, 1)}
    40% {-webkit-transform: scale3d(0.75, 1.2, 1);transform: scale3d(0.75, 1.2, 1)}
    50% {-webkit-transform: scale3d(1.15, 0.85, 1);transform: scale3d(1.15, 0.85, 1); opacity: 1;}
    65% {-webkit-transform: scale3d(.95, 1.05, 1);transform: scale3d(.95, 1.05, 1)}
    75% {-webkit-transform: scale3d(1.05, .95, 1);transform: scale3d(1.05, .95, 1)}
    to {-webkit-transform: scale3d(1, 1, 1);transform: scale3d(1, 1, 1); opacity: 1;}
}
@keyframes aboutFadeIn {
    from {-webkit-transform: scale3d(1, 1, 1);transform: scale3d(1, 1, 1); opacity: 0;}
    30% {-webkit-transform: scale3d(1.25, 0.75, 1);transform: scale3d(1.25, 0.75, 1)}
    40% {-webkit-transform: scale3d(0.75, 1.2, 1);transform: scale3d(0.75, 1.2, 1)}
    50% {-webkit-transform: scale3d(1.15, 0.85, 1);transform: scale3d(1.15, 0.85, 1); opacity: 1;}
    65% {-webkit-transform: scale3d(.95, 1.05, 1);transform: scale3d(.95, 1.05, 1)}
    75% {-webkit-transform: scale3d(1.05, .95, 1);transform: scale3d(1.05, .95, 1)}
    to {-webkit-transform: scale3d(1, 1, 1);transform: scale3d(1, 1, 1); opacity: 1;}
}
